<template><markdown-wrapper><h1 id="navbar">Navbar <a class="header-anchor" href="#navbar" aria-hidden="true">&#128279;</a></h1>
<blockquote>
<p>Navbars are responsive meta components that serve as navigation headers for your application or site.</p>
</blockquote>
<p><ins class="adsbygoogle"
     style="display:block; text-align:center;"
     data-ad-layout="in-article"
     data-ad-format="fluid"
     data-ad-client="ca-pub-4714899946256166"
     data-ad-slot="4603582855"></ins></p>
<h2 id="example">Example <a class="header-anchor" href="#example" aria-hidden="true">&#128279;</a></h2>
<p>Contents in <code v-pre>collapse</code> slot can be collapsed (and are toggleable) in mobile views and become horizontal as the available viewport width increases.</p>
<navbar-example ref="navbar-example"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">navbar</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"navbar-brand"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"brand"</span> <span class="hljs-attr">href</span>=<span class="hljs-string">"#"</span>&gt;</span>Brand<span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"collapse"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">navbar-nav</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">li</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"active"</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">role</span>=<span class="hljs-string">"button"</span>&gt;</span>Link <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"sr-only"</span>&gt;</span>(current)<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">li</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">li</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">role</span>=<span class="hljs-string">"button"</span>&gt;</span>Link<span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">li</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">navbar-nav</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">navbar-form</span> <span class="hljs-attr">left</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"form-group"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">input</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"form-control"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"Search"</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">btn</span>&gt;</span>Submit<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">navbar-form</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">navbar-nav</span> <span class="hljs-attr">right</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">li</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">role</span>=<span class="hljs-string">"button"</span>&gt;</span>Link<span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">li</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">dropdown</span> <span class="hljs-attr">tag</span>=<span class="hljs-string">"li"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"dropdown-toggle"</span> <span class="hljs-attr">role</span>=<span class="hljs-string">"button"</span>&gt;</span>Dropdown <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"caret"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"dropdown"</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">li</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">role</span>=<span class="hljs-string">"button"</span>&gt;</span>Action<span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">li</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">li</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">role</span>=<span class="hljs-string">"button"</span>&gt;</span>Another action<span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">li</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">li</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">role</span>=<span class="hljs-string">"button"</span>&gt;</span>Something else here<span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">li</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">li</span> <span class="hljs-attr">role</span>=<span class="hljs-string">"separator"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"divider"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">li</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">li</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">role</span>=<span class="hljs-string">"button"</span>&gt;</span>Separated link<span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">li</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">dropdown</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">navbar-nav</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">navbar</span>&gt;</span>
<span class="hljs-comment">&lt;!-- navbar-example.vue --&gt;</span>
</code></pre>
<h2 id="brand-image">Brand image <a class="header-anchor" href="#brand-image" aria-hidden="true">&#128279;</a></h2>
<p>Replace the navbar brand with your own image by swapping the text for an <code v-pre>&lt;img&gt;</code>. Since the <code v-pre>.navbar-brand</code> has its own padding and height, you may need to override some CSS depending on your image.</p>
<navbar-image ref="navbar-image"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">navbar</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"navbar-brand"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"brand"</span> <span class="hljs-attr">href</span>=<span class="hljs-string">"#"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">img</span> <span class="hljs-attr">alt</span>=<span class="hljs-string">"Brand"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"height: 100%; width: auto"</span> <span class="hljs-attr">src</span>=<span class="hljs-string">"data:image/png;base64,"</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">navbar</span>&gt;</span>
<span class="hljs-comment">&lt;!-- navbar-image.vue --&gt;</span>
</code></pre>
<h2 id="forms">Forms <a class="header-anchor" href="#forms" aria-hidden="true">&#128279;</a></h2>
<p>Place form content within <code v-pre>&lt;navbar-form&gt;</code> for proper vertical alignment and collapsed behavior in narrow viewports. Use the alignment props <code v-pre>left</code> / <code v-pre>right</code> to decide where it resides within the navbar content.</p>
<navbar-form ref="navbar-form"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">navbar</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"navbar-brand"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"brand"</span> <span class="hljs-attr">href</span>=<span class="hljs-string">"#"</span>&gt;</span>Brand<span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">navbar-form</span> <span class="hljs-attr">left</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"form-group"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">input</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"text"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"form-control"</span> <span class="hljs-attr">placeholder</span>=<span class="hljs-string">"Search"</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">btn</span>&gt;</span>Submit<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">navbar-form</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">navbar</span>&gt;</span>
<span class="hljs-comment">&lt;!-- navbar-form.vue --&gt;</span>
</code></pre>
<h2 id="buttons">Buttons <a class="header-anchor" href="#buttons" aria-hidden="true">&#128279;</a></h2>
<p>Add the <code v-pre>.navbar-btn</code> class to <code v-pre>&lt;btn&gt;</code> that not residing in a <code v-pre>&lt;navbar-form&gt;</code> to vertically center them in the navbar.</p>
<navbar-button ref="navbar-button"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">navbar</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"navbar-brand"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"brand"</span> <span class="hljs-attr">href</span>=<span class="hljs-string">"#"</span>&gt;</span>Brand<span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">btn</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"navbar-btn"</span>&gt;</span>Sign in<span class="hljs-tag">&lt;/<span class="hljs-name">btn</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">navbar</span>&gt;</span>
<span class="hljs-comment">&lt;!-- navbar-button.vue --&gt;</span>
</code></pre>
<h2 id="text">Text <a class="header-anchor" href="#text" aria-hidden="true">&#128279;</a></h2>
<p>Wrap strings of text in <code v-pre>&lt;navbar-text&gt;</code> tag for proper leading and color.</p>
<navbar-text ref="navbar-text"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">navbar</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"navbar-brand"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"brand"</span> <span class="hljs-attr">href</span>=<span class="hljs-string">"#"</span>&gt;</span>Brand<span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">navbar-text</span>&gt;</span>Signed in as wxsm<span class="hljs-tag">&lt;/<span class="hljs-name">navbar-text</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">navbar</span>&gt;</span>
<span class="hljs-comment">&lt;!-- navbar-text.vue --&gt;</span>
</code></pre>
<h2 id="non-nav-links">Non-nav links <a class="header-anchor" href="#non-nav-links" aria-hidden="true">&#128279;</a></h2>
<p>For folks using standard links that are not within the regular navbar navigation component, use the <code v-pre>.navbar-link</code> class to add the proper colors for the default and inverse navbar options.</p>
<navbar-links ref="navbar-links"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">navbar</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"navbar-brand"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"brand"</span> <span class="hljs-attr">href</span>=<span class="hljs-string">"#"</span>&gt;</span>Brand<span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"collapse"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">navbar-text</span> <span class="hljs-attr">right</span>&gt;</span>Signed in as <span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">href</span>=<span class="hljs-string">"#"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"navbar-link"</span>&gt;</span>wxsm<span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">navbar-text</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">navbar</span>&gt;</span>
<span class="hljs-comment">&lt;!-- navbar-links.vue --&gt;</span>
</code></pre>
<h2 id="fixed-navbar">Fixed navbar <a class="header-anchor" href="#fixed-navbar" aria-hidden="true">&#128279;</a></h2>
<p>Add <code v-pre>fixed-top</code> / <code v-pre>fixed-bottom</code> to make navbar fixed.</p>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">navbar</span> <span class="hljs-attr">fixed-top</span>&gt;</span>
  ...
<span class="hljs-tag">&lt;/<span class="hljs-name">navbar</span>&gt;</span>
</code></pre>
<p>Note that the fixed navbar will overlay your other content, unless you add <code v-pre>padding</code> to the top of the <code v-pre>&lt;body&gt;</code>. Try out your own values or use our snippet below. Tip: By default, the navbar is <code v-pre>50px</code> high.</p>
<pre v-pre><code v-pre class="language-css"><span class="hljs-selector-tag">body</span> { <span class="hljs-attribute">padding-top</span>: <span class="hljs-number">70px</span>; }
</code></pre>
<p>Make sure to include this <strong>after</strong> the core Bootstrap CSS.</p>
<h2 id="static-top">Static top <a class="header-anchor" href="#static-top" aria-hidden="true">&#128279;</a></h2>
<p>Create a full-width navbar that scrolls away with the page by adding <code v-pre>static-top</code> prop.</p>
<p>Unlike fixed navbars, you do not need to change any padding on the <code v-pre>body</code>.</p>
<navbar-static-top ref="navbar-static-top"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">navbar</span> <span class="hljs-attr">static-top</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"navbar-brand"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"brand"</span> <span class="hljs-attr">href</span>=<span class="hljs-string">"#"</span>&gt;</span>Brand<span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">navbar</span>&gt;</span>
<span class="hljs-comment">&lt;!-- navbar-static-top.vue --&gt;</span>
</code></pre>
<h2 id="inverted-navbar">Inverted navbar <a class="header-anchor" href="#inverted-navbar" aria-hidden="true">&#128279;</a></h2>
<p>Modify the look of the navbar by adding <code v-pre>inverse</code> prop.</p>
<navbar-inverse ref="navbar-inverse"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">navbar</span> <span class="hljs-attr">inverse</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"navbar-brand"</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"brand"</span> <span class="hljs-attr">href</span>=<span class="hljs-string">"#"</span>&gt;</span>Brand<span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"collapse"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">navbar-nav</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">li</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"active"</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">role</span>=<span class="hljs-string">"button"</span>&gt;</span>Link <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"sr-only"</span>&gt;</span>(current)<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">li</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">li</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">role</span>=<span class="hljs-string">"button"</span>&gt;</span>Link<span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">li</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">navbar-nav</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">navbar</span>&gt;</span>
<span class="hljs-comment">&lt;!-- navbar-inverse.vue --&gt;</span>
</code></pre>
<h1 id="api-reference">API Reference <a class="header-anchor" href="#api-reference" aria-hidden="true">&#128279;</a></h1>
<h2 id="navbar-2"><a href="https://github.com/wxsms/uiv/blob/master/src/components/navbar/Navbar.vue">Navbar</a> <a class="header-anchor" href="#navbar-2" aria-hidden="true">&#128279;</a></h2>
<h3 id="props">Props <a class="header-anchor" href="#props" aria-hidden="true">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Type</th>
<th>Default</th>
<th>Required</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>v-model</code></td>
<td>Boolean</td>
<td></td>
<td></td>
<td>Indicate the collapse status of navbar.</td>
</tr>
<tr>
<td><code v-pre>fluid</code></td>
<td>Boolean</td>
<td>true</td>
<td></td>
<td>Use <code v-pre>.container-fluid</code> class in navbar container, <code v-pre>.container</code> otherwise.</td>
</tr>
<tr>
<td><code v-pre>fixed-top</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Apply fixed top style.</td>
</tr>
<tr>
<td><code v-pre>fixed-bottom</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Apply fixed bottom style.</td>
</tr>
<tr>
<td><code v-pre>static-top</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Apply static top style.</td>
</tr>
<tr>
<td><code v-pre>inverse</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Apply inverse style.</td>
</tr>
</tbody>
</table></div><h3 id="slots">Slots <a class="header-anchor" href="#slots" aria-hidden="true">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>default</code></td>
<td>The navbar body (non-collapsable part).</td>
</tr>
<tr>
<td><code v-pre>collapse</code></td>
<td>The navbar body (collapsable part).</td>
</tr>
<tr>
<td><code v-pre>brand</code></td>
<td>The navbar brand.</td>
</tr>
<tr>
<td><code v-pre>collapse-btn</code></td>
<td>Use this slot to override the default collapse button.</td>
</tr>
</tbody>
</table></div><h2 id="navbarnav"><a href="https://github.com/wxsms/uiv/blob/master/src/components/navbar/NavbarNav.js">NavbarNav</a> <a class="header-anchor" href="#navbarnav" aria-hidden="true">&#128279;</a></h2>
<h3 id="props-2">Props <a class="header-anchor" href="#props-2" aria-hidden="true">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Type</th>
<th>Default</th>
<th>Required</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>left</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Pull content to left.</td>
</tr>
<tr>
<td><code v-pre>right</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Pull content to right.</td>
</tr>
</tbody>
</table></div><h2 id="navbarform"><a href="https://github.com/wxsms/uiv/blob/master/src/components/navbar/NavbarForm.js">NavbarForm</a> <a class="header-anchor" href="#navbarform" aria-hidden="true">&#128279;</a></h2>
<h3 id="props-3">Props <a class="header-anchor" href="#props-3" aria-hidden="true">&#128279;</a></h3>
<div class="table-responsive"><table class="table table-bordered table-hover"><thead>
<tr>
<th>Name</th>
<th>Type</th>
<th>Default</th>
<th>Required</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>left</code></td>
<td>Boolean</td>
<td>false</td>
<td></td>
<td>Pull content to left.</td>
</tr>
<tr>
<td><code v-pre>right</code></td>
<td>Boolean</td>
